.btn {
    background-color: transparent;
    border: unset;
    font-family: $font-display;
    font-weight: 500;
    font-size: $p;
    color: $white;
    padding: 16px 24px;
    transition: all 300ms ease;
    &-primary {
        color: $white;
        border-radius: 24px;
        background-color: $black;
    }
    &-link {
        color: $black;
    }
    &-subscription {
        color: $white;
        padding: 16px;
        border-radius: 24px;
        background: linear-gradient(99deg, #4200ff 0%, #22e4ff 55.73%, #80ff00 100%);
        &-lg {
            display: inline-flex;
            flex-direction: column;
            padding: 24px;
            border-radius: 24px;
            background: linear-gradient(99deg, #4200ff 0%, #22e4ff 55.73%, #9eff00 100%);
        }
        &-dark {
            padding: 48px 36px;
            border-radius: 24px;
            background: linear-gradient(
                99deg,
                rgba(66, 0, 255, 0) 0%,
                rgba(34, 228, 255, 0.2) 55.73%,
                rgba(158, 255, 0, 0.2) 100%
            );
        }
    }
    &-arrow {
        max-width: 32px;
        padding: 0;
    }
}
